body {
    background-color: #fff;
    overflow-x: hidden;
    font-family: 'Avant-Garde-Std-Book';
}

nav {
    z-index: 99;
    .logo {
        background-image: url('../img/desktop/logo_nav.png');
        background-repeat: no-repeat;
        height: 64px;
        width: 87px;
        image-rendering: -webkit-optimize-contrast;
        background-size: cover;
    }
}

#home {
    // background-image: url('../img/imageacceuil.png');
    width: 100vw;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: #f2f2f2;
    .blurred {
        background-image: url('https://www.renovationettravaux.fr/wp-content/uploads/2019/09/Calculer-le-prix-de-la-ma%C3%A7onnerie-g%C3%A9n%C3%A9rale.jpg');
        background-size: cover;
        position: absolute;
        left: 0;
        width: 100vw;
        height: calc(100vh - 90px);
        z-index: -1;
        filter: blur(1px) brightness(0.85);
        zoom: 1.02;
        left: -20px;
        top: -10px;
    }
    .logo {
        margin: auto;
        display: block;
        padding-top: 5rem;
    }
    #typewriter {
        font-size: 3.5rem;
    }
    #callToAction {
        font-size: 1.6rem;
        border-radius: 13px;
    }
}

#whatwedo {
    background-color: #f2f2f2;
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 45vh;
    width: 100vw;
    min-height: 100vh;
    @media (max-width: 991.98px) {
        #titleImage {
            width: 80%;
        }
    }
    @media (min-width: 992px) {
        background-image: url('../img/desktop/top-right-bg.png');
        #titleImage {
            width: 20%;
        }
    }
    .label-number {
        background-image: url('../img/desktop/number_pattern.png');
        background-repeat: no-repeat;
        background-position: top left;
        background-size: contain;
        color: #FFF;
        text-align: center;
        @media (max-width: 991.98px) {
            width: 48px;
            height: 46px;
            font-size: 2rem;
        }
        @media (min-width: 992px) {
            width: 64px;
            height: 63px;
            font-size: 2.5rem;
        }
    }
    .service_img {
        max-width: 50%;
    }
    .service_title::first-letter {
        color: #1a73b9;
    }
}

#project {
    width: 100vw;
    height: 50vh;
    #leftContainer {
        background-image: url('../img/desktop/photoLeft.png');
        background-repeat: no-repeat;
        background-size: cover;
        height: 50vh;
    }
    #rightContainer {
        background-color: #333333;
        height: 50vh;
        #rightIllu {
            height: 40%;
        }
    }
}

#trust {
    width: 100vw;
    min-height: 50vh;
    .description {
        font-size: 1.2rem;
    }
}

#footer {
    background-color: #f2f2f2;
    .logo {
        max-width: 35%;
    }
    .footer-title {
        color: #1a73b9;
    }
    ul {
        list-style: none;
    }
    li {
        padding: .35rem;
        a {
            color: #333333;
            font-weight: 400;
            text-decoration: none;
        }
        a:hover {
            color: #1a73b9;
            font-weight: 400;
        }
    }
}

#contact {
    background-color: #f2f2f2;
}

.btn a {
    text-decoration: none;
    color: unset;
}